export function validateSolidityTypeInstance(
  value: JSBI,
  solidityType: SolidityType
): void {
  invariant(
    JSBI.greaterThanOrEqual(value, ZERO),
    `${value} is not a ${solidityType}.`
  )
  invariant(
    JSBI.lessThanOrEqual(value, SOLIDITY_TYPE_MAXIMA[solidityType]),
    `${value} is not a ${solidityType}.`
  )
}

// warns if addresses are not checksummed
export function validateAndParseAddress(
  address: string,
  chainId: ChainId
): string {
  try {
    if (chainId === ChainId.SN_MAIN || chainId === ChainId.SN_SEPOLIA) {
      return address
    }
    const checksummedAddress = getAddress(address)
    warning(address === checksummedAddress, `${address} is not checksummed.`)
    return checksummedAddress
  } catch (error) {
    invariant(false, `${address} is not a valid address.`)
  }
}

export function parseBigintIsh(bigintIsh: BigintIsh): JSBI {
  return bigintIsh instanceof JSBI
    ? bigintIsh
    : typeof bigintIsh === 'bigint'
    ? JSBI.BigInt(bigintIsh.toString())
    : JSBI.BigInt(bigintIsh)
}

// mock the on-chain sqrt function
export function sqrt(y: JSBI): JSBI {
  validateSolidityTypeInstance(y, SolidityType.uint256)
  let z: JSBI = ZERO
  let x: JSBI
  if (JSBI.greaterThan(y, THREE)) {
    z = y
    x = JSBI.add(JSBI.divide(y, TWO), ONE)
    while (JSBI.lessThan(x, z)) {
      z = x
      x = JSBI.divide(JSBI.add(JSBI.divide(y, x), x), TWO)
    }
  } else if (JSBI.notEqual(y, ZERO)) {
    z = ONE
  }
  return z
}

// given an array of items sorted by `comparator`, insert an item into its sort index and constrain the size to
// `maxSize` by removing the last item
export function sortedInsert<T>(
  items: T[],
  add: T,
  maxSize: number,
  comparator: (a: T, b: T) => number
): T | null {
  invariant(maxSize > 0, 'MAX_SIZE_ZERO')
  // this is an invariant because the interface cannot return multiple removed items if items.length exceeds maxSize
  invariant(items.length <= maxSize, 'ITEMS_SIZE')

  // short circuit first item add
  if (items.length === 0) {
    items.push(add)
    return null
  } else {
    const isFull = items.length === maxSize
    // short circuit if full and the additional item does not come before the last item
    if (isFull && comparator(items[items.length - 1], add) <= 0) {
      return add
    }

    let lo = 0
    let hi = items.length

    while (lo < hi) {
      const mid = (lo + hi) >>> 1
      if (comparator(items[mid], add) <= 0) {
        lo = mid + 1
      } else {
        hi = mid
      }
    }
    items.splice(lo, 0, add)
    return isFull ? items.pop()! : null
  }
}

export function supportContractWithPrice(chainId: ChainId): boolean {
  return !!ROUTER_ADDRESS_WITH_PRICE[chainId]
}

export function isRouterV2(chainId: ChainId): boolean {
  return !!PYTH_ADDRESS[chainId] && !ROUTER_ADDRESS_WITH_PRICE[chainId]
}

export function isTestnetSkipAmountsMin(chainId: ChainId): boolean {
  return false
}

/**
 * Use when AddLiquidity v2 - Arbitrum Sepolia, Arbitrum One
 */
export const getPythPrice = async (address?: string, chainId?: ChainId) => {
  if (!address || !chainId) return 0

  try {
    const web3 = new Web3(new Web3.providers.HttpProvider(RPC_URLS[chainId]))

    const factoryContract = new web3.eth.Contract(
      IFactoryV2 as any,
      FACTORY_ADDRESS[chainId]
    )

    const pythContract = new web3.eth.Contract(
      IPythUpgradable as any,
      PYTH_ADDRESS[chainId]
    )

    const priceFeedId = await factoryContract.methods
      .priceFeedIds(address)
      .call()

    const priceUnsafe = await pythContract.methods
      .getPriceUnsafe(priceFeedId)
      .call()

    const [price, conf, expo, publishTime] = priceUnsafe

    return +price * 10 ** +expo
  } catch (error) {
    console.warn('Cannot get Pyth price', error)
    return 0
  }
}

export const getDataBytes = async (
  chainId: ChainId,
  addresses: Array<string | undefined>
) => {
  const web3 = new Web3(new Web3.providers.HttpProvider(RPC_URLS[chainId]))

  const factoryContract = new web3.eth.Contract(
    IFactoryV2 as any,
    FACTORY_ADDRESS[chainId]
  )

  const priceFeedIds = await Promise.all(
    addresses.map(async (address) => {
      const priceFeedId: number = await factoryContract.methods
        .priceFeedIds(address)
        .call()
      return priceFeedId
    })
  )

  const { data: dataBytes }: AxiosResponse<{ binary: { data: string[] } }> =
    await axios.get('https://hermes.pyth.network/v2/updates/price/latest', {
      params: {
        ids: priceFeedIds
      }
    })

  return dataBytes.binary.data.map((byte) => `0x${byte}`)
}

export const solidityPack = async (
  chainId: ChainId,
  addresses: Array<string | undefined>
) => {
  const dataBytes = await getDataBytes(chainId, addresses)
  // return ethers.utils
  //   .solidityPack(
  //     ['bytes'],
  //     []
  //   )
  //   .substring(0, 2)
  return ethers.utils.defaultAbiCoder.encode(['bytes[]'], [dataBytes])
}
